@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Lato", sans-serif}h1,h2,h3{color:#363636}h3,h4{font-weight:400;color:#646464}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex}.time-control input{width:100%;padding:1rem 0rem}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:30%}.play-control svg{cursor:pointer}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between;padding-top:1.5rem}.song-container img{width:15%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background-color:white;box-shadow:2px 2px 30px rgba(0,0,0,0.7);overflow:scroll;transform:translateX(-100%);transition:all 0.5s ease;opacity:0}.library h2{padding:2rem}.library-song{display:flex;align-items:center;padding:1rem 2rem;cursor:pointer}.library-song img{width:30%}.library-song:hover{background:#c4c4c4}.song-description{padding-left:1rem}.song-description h3{font-size:1rem}.song-description h4{font-size:0.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.selected{background:#a5bae2}.active-library{transform:translateX(0%);opacity:1}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;border:none;cursor:pointer;border:2px solid #414141;padding:0.5rem;transition:all 0.3s ease}nav button:hover{background:#414141;color:white}

